import React, { Component } from 'react'
import wikipedia from '../helper/wikipedia'
import Collapsed from './Collapsed'

const WikipediaLink = ({name}) =>
  <a href={`https://en.wikipedia.org/wiki/${encodeURI(name)}`}>Wikipedia</a>

export default class Place extends Component {

  constructor(props) {
    super(props)
    this.state = {
      extract: ''
    }
  }

  async componentDidMount() {
    const page = await wikipedia(this.props.name)
    if (page) {
      const { extract } = page
      this.setState({extract})
    }
  }

  render() {
    const { name, url, icon } = this.props
    const { extract } = this.state
    return (
      <li className="place">
        <img src={icon} alt="icon for type of place" />
        <a href={url}>{name}</a>
        {extract &&
          <Collapsed>
            <p className="extract">{extract}</p>
            <p>Source: <WikipediaLink name={name} /></p>
          </Collapsed>}
      </li>
    )
  }

}



