const call = (service, request) =>
  new Promise((resolve, reject) =>
    service.nearbySearch(request, (results, status) =>
      status === window.google.maps.places.PlacesServiceStatus.OK || window.google.maps.places.PlacesServiceStatus.ZERO_RESULTS
      ? resolve(results)
      : reject(status))
  )

export default async (latitude, longitude) => {
  const pyrmont = new window.google.maps.LatLng(latitude, longitude)

  const map = new window.google.maps.Map(document.getElementById('map'), {
    center: pyrmont,
    zoom: 15
  })

  const request = {
    location: pyrmont,
    radius: '50',
    rankby: 'distance',
    // types: ['amusement_park', 'aquarium','art_gallery','church','city_hall','courthouse','embassy','hindu_temple','library','local_government_office','mosque','museum','park','stadium','synagogue','zoo']
  }

  const service = new window.google.maps.places.PlacesService(map);
  const response = await call(service, request)
  return response;
}



