import React, { Component } from 'react';
import Fetch from './component/Fetch';
import './App.css';

class App extends Component {
  render() {
    return (
      <div className="App">
        <header className="App-header">
          <h1 className="App-title">Wing!pedia</h1>
        </header>
        <Fetch />
      </div>
    );
  }
}

export default App;



