import { keys } from 'lodash/fp'

/**
 * Calls Wikipedia webservice.
 *
 * @example https://en.wikipedia.org/w/api.php?format=json&action=query&prop=extracts&exintro=&explaintext=&titles=Munich
 */
const addScriptTag = url => {
  const script = document.createElement('script');
  script.type = 'text/javascript';
  script.src = url;
  document.body.appendChild(script);
}

export default title =>
  new Promise((resolve, reject) => {
    const callbackFunctionName = 'apiCallbackFor' + title.replace(/[^a-z]/ig, '_')
    const serviceUrl = 'https://en.wikipedia.org/w/api.php'
    const params = `format=json&action=query&prop=extracts&exintro=&explaintext=&titles=${encodeURI(title)}&callback=${callbackFunctionName}`
    const url = `${serviceUrl}?${params}`

    addScriptTag(url)

    window[callbackFunctionName] = result => {
      const pages = result.query.pages
      const pageId = keys(pages)[0]
      if (pageId !== '-1') return resolve(pages[pageId])
      return resolve()
    }
  })



