import React, { Component } from 'react'
import { Machine, actions } from 'xstate'
import { interpret } from 'xstate/lib/interpreter'
import geo from '../helper/geo'
import maps from '../helper/maps'
import places from '../helper/places'
import Loading from './Loading';
import Results from './Results';
import Error from './Error';

const { assign } = actions;

const fetchPlaces = async () => {
  const { latitude, longitude } = await geo()
  const results = await maps(latitude, longitude)
  return places(results)
}

const promiseMachine = Machine({
  id: 'promise',
  context: {
    places: []
  },
  initial: 'pending',
  states: {
    pending: {
      invoke: {
        id: 'getPlaces',
        src: () => fetchPlaces(),
        onDone: {
          target: 'resolved',
          actions: assign({ places: (ctx, event) => event.data })
        },
        onError: {
          target: 'rejected',
          actions: assign({ error: (ctx, event) => event.data })
        }
      },
    },
    resolved: {
      type: 'final'
    },
    rejected: {
      type: 'final'
    }
  }
});

export default class Location extends Component {

  state = {
    current: promiseMachine.initialState,
  }

  service = interpret(promiseMachine)
    .onTransition(current => this.setState({ current }))

  componentDidMount() {
    this.service.start()
  }

  componentWillUnmount() {
    this.service.stop()
  }

  render() {
    const { current } = this.state
    const { places } = current.context

    const isPending = current.matches('pending')
    const isResolved = current.matches('resolved')

    if (isPending) {
      return <Loading />
    } else if (isResolved) {
      return <Results places={places} />
    }

    return <Error />
  }

}



