export default () =>
  new Promise((resolve, reject) => {
    if (!navigator) reject(new Error('Navigator missing'))

    // const { latitude, longitude } = {latitude: 51.3397644, longitude: 12.3704587}
    // const { latitude, longitude } = {latitude: 48.1295803, longitude: 11.5780538}
    // return resolve({latitude, longitude})
    navigator.geolocation.getCurrentPosition(position => {
      const { latitude, longitude } = position.coords;
      return resolve({latitude, longitude})
    })
  }
)



