import React from 'react'
import { Machine } from 'xstate'
import { interpret } from 'xstate/lib/interpreter'

const toggleMachine = Machine({
  id: 'toggle',
  initial: 'inactive',
  states: {
    inactive: {
      on: { TOGGLE: 'active' }
    },
    active: {
      on: { TOGGLE: 'inactive' }
    }
  }
})

class Collapsed extends React.Component {
  state = {
    current: toggleMachine.initialState
  }

  service = interpret(toggleMachine)
    .onTransition(current => this.setState({ current }))

  componentDidMount() {
    this.service.start()
  }

  componentWillUnmount() {
    this.service.stop()
  }

  render() {
    const { children } = this.props
    const { current } = this.state
    const { send } = this.service
    const isActive = current.matches('active')

    return (
      <div className="Collapsed">
        <button onClick={() => send('TOGGLE')}>Show {isActive ? 'less': 'more'}:</button>
        <div className={isActive ? 'uncollapsed' : 'collapsed'} >
          {children}
        </div>
      </div>
    )
  }
}

export default Collapsed



