import React, {Component} from 'react'
import { Machine } from 'xstate'
import { interpret } from 'xstate/lib/interpreter'
import Location from './Location';
import NoPlaces from './NoPlaces';

const resultsMachine = Machine({
  id: 'results',
  context: {
    places: []
  },
  initial: 'empty',
  states: {
    empty: {
      on: {
        '': {
          target: 'results',
          cond: (ctx) => ctx.places.length > 0
        }
      }
    },
    results: {
      type: 'final'
    }
  }
});

class Results extends Component {
  constructor(props) {
    super(props)
    const { places } = props
    const machine = resultsMachine.withContext({places})

    this.state = {
      current: machine.initialState,
    }

    this.service = interpret(machine).onTransition(
      current => this.setState({ current })
    )
  }

  componentDidMount() {
    this.service.start()
  }

  componentWillUnmount() {
    this.service.stop()
  }

  render() {
    const { current } = this.state
    const { places } = current.context

    const isEmpty = current.matches('empty')
    const hasResults = current.matches('results')

    return (
      <React.Fragment>
        {isEmpty && <NoPlaces />}
        {hasResults && <Location places={places} />}
      </React.Fragment>
    )
  }
}

export default Results



